<template>
  <div class="container">
    <div class="form">
      <el-form label-width="120px" :inline="true" :model="fleet">
        <el-form-item label="机构代码:" prop="companyCode">
          <el-input v-model="fleet.companyCode"></el-input>
        </el-form-item>
        <el-form-item label="投保人:" prop="PolicyHolder">
          <el-input v-model="fleet.PolicyHolder"></el-input>
        </el-form-item>
        <el-form-item label="协议号:" prop="ProtocolCode">
          <el-input v-model="fleet.ProtocolCode"></el-input>
        </el-form-item>
        <el-form-item label="归属业务员:" prop="salesMan">
          <el-input v-model="fleet.salesMan"></el-input>
        </el-form-item>
        <el-form-item label="是否包含下级:" prop="isIncludeSub">
          <el-select v-model="fleet.isIncludeSub">
            <el-option label="是" value="y"></el-option>
            <el-option label="否" value="n"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="排序:" prop="sort">
          <el-cascader
            expand-trigger="hover"
            :options="options"
            v-model="fleet.sort">
          </el-cascader>
        </el-form-item>
        <el-form-item label="提交时间:" prop="submitTime">
          <el-col :span="11">
            <el-date-picker
              type="date"
              placeholder="请选择时间段"
              v-model="fleet.submitTimeStart"
              style="width: 100%;">
            </el-date-picker>
          </el-col>
          <el-col style="text-align: center" :span="2">-</el-col>
          <el-col :span="11">
            <el-date-picker
              type="date"
              placeholder="请选择时间段"
              v-model="fleet.submitTimeEnd"
              style="width: 100%;">
            </el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="申报状态:" prop="status">
          <el-checkbox-group v-model="fleet.status">
            <el-checkbox label="待处理" name="1"></el-checkbox>
            <el-checkbox label="正在处理" name="2"></el-checkbox>
            <el-checkbox label="已处理未流转" name="3"></el-checkbox>
            <el-checkbox label="已处理流转" name="4"></el-checkbox>
            <el-checkbox label="已处理完毕" name="5"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
    </div>
    <div class="option">
      <el-button type="primary" size="small" plain @click="reset">重置</el-button>
      <el-button type="primary" size="small" plain @click="query">查询</el-button>
    </div>
    <div class="data">
      <div class="table">
        <base-table
          :columns="columns"
          :data="currentData"
          :show-header="true"
          row-key="ProtocolCode"
          @detail="detail">
        </base-table>
        <communal-pagination
          @search="search"
          :currentPage="currentPage"
          :pageSize="pageSize"
          :total="data.length">
        </communal-pagination>
      </div>
    </div>
  </div>
</template>

<script>
const BaseTable = () => import('@/components/BaseTable')
const CommunalPagination = () => import('@/components/CommunalPagination')

export default {
  name: 'index',
  components: {BaseTable, CommunalPagination},
  data () {
    return {
      currentPage: 1,
      pageSize: 2,
      fleet: {
        companyCode: '',
        PolicyHolder: '',
        ProtocolCode: '',
        salesMan: '',
        isIncludeSub: 'y',
        sort: [],
        submitTimeStart: '',
        submitTimeEnd: '',
        status: []
      },
      columns: [
        {prop: 'ProtocolCode', width: 140, label: '代理协议号', align: 'center'},
        {prop: 'ProxyHolder', width: 100, label: '代理人名称', align: 'center'},
        {prop: 'company', width: 260, label: '归属机构', align: 'center'},
        {prop: 'PolicyHolder', width: 120, label: '投保人', align: 'center'},
        {prop: 'inflowTime', width: 130, label: '流入时间', align: 'center'},
        {prop: 'level', width: 100, label: '级别', align: 'center'},
        {prop: 'status', width: 100, label: '状态', align: 'center', status: true},
        {prop: 'handler', width: 100, label: '处理人', align: 'center'},
        {prop: 'submitUser', width: 100, label: '提交人', align: 'center'},
        {prop: 'firstSubmit', width: 130, label: '第一次提交时间', align: 'center'},
        {prop: 'operation', width: 100, label: '操作', align: 'center', operation: true, operations: [{text: '查看', event: 'detail'}]}
      ],
      data: [
        {
          ProtocolCode: '5300800002016000004',
          ProxyHolder: '张刚',
          company: '山东省分公司济南中心支公司营业部',
          PolicyHolder: '济南市采购中心',
          inflowTime: '2016-03-21 18:28:34',
          level: '初审1',
          status: '正在处理',
          handler: '王伟',
          submitUser: '白静',
          firstSubmit: '2016-03-21 18:28:34'
        },
        {
          ProtocolCode: '5100400002016000003',
          ProxyHolder: '白宇',
          company: '山东省分公司烟台中心支公司营业部',
          PolicyHolder: '烟台水泵厂',
          inflowTime: '2016-05-04 11:59:10',
          level: '初级',
          status: '已处理未流转',
          handler: '王伟',
          submitUser: '刘志民',
          firstSubmit: '2016-05-04 11:57:02'
        },
        {
          ProtocolCode: '5330400002015000001',
          ProxyHolder: '刘明',
          company: '山东省分公司潍坊中心支公司营业部',
          PolicyHolder: '合力物流运输有限责任公司',
          inflowTime: '2016-05-04 11:59:10',
          level: '初审1',
          status: '已处理流转',
          handler: '李路',
          submitUser: '李路',
          firstSubmit: '2016-05-04 16:23:48'
        }
      ],
      options: [
        {
          value: 'level',
          label: '级别',
          children: [
            {
              value: 'asc',
              label: '升序'
            },
            {
              value: 'desc',
              label: '降序'
            }
          ]
        },
        {
          value: 'firstSubmit',
          label: '第一次提交时间',
          children: [
            {
              value: 'asc',
              label: '升序'
            },
            {
              value: 'desc',
              label: '降序'
            }
          ]
        },
        {
          value: 'company',
          label: '机构',
          children: [
            {
              value: 'asc',
              label: '升序'
            },
            {
              value: 'desc',
              label: '降序'
            }
          ]
        }
      ]
    }
  },
  computed: {
    currentData () {
      const pageData = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end <= this.data.length ? this.currentPage * this.pageSize : this.data.length
      for (let i = start; i < end; ++i) {
        pageData.push(this.data[i])
      }
      return pageData
    }
  },
  methods: {
    reset () {},
    query () {
      console.log(this.fleet)
    },
    detail (id) {
      this.$router.push(`/admin/fleet/query/detail/${id}`)
    },
    search (pageNumber, pageSize) {
      this.currentPage = pageNumber
      this.pageSize = pageSize
    }
  }
}
</script>

<style scoped>

</style>



